.App {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

nav {
  height: 2.5em;
  margin: 0;
  font-size: 1.25em;

  overflow: hidden;

  border-radius: 4px;
  background-color: blue;
}

.navbar {
  height: 100%;
  width: calc(100% - 40px);
  margin: 0;
  background-color: inherit;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.navbar > li {
  padding: 0.75em;

  float:left;
  display: block;
  list-style: none;
}

.navbar  a {
  color: white;
  font-weight: bold;
  text-decoration: none;
}

.menu-button {
  height: 3.5em;
  padding: 0;
  margin: 0.5em;
  border: none;
  cursor: pointer;
  background: transparent;
  float: right;
}

.menu-button > img {
  height: 100%;
}

nav.show-nav {
  height: 12.5em;
}

@media screen and (min-width: 768px) {
  nav.show-nav {
    height: 2.5em;
  }

  .menu-button {
    display: none;
  }

  .navbar {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .navbar > li {
    height: unset;
    padding: 0 15px;
  }
}

.title-bar {
  width: 100%;
  display: inline-block;
}

.title {
  float: left;
  font-size: 2rem;
}

.contact-info {
  margin-right: 10px;
  float: right;
}

ul.contact-info {
  list-style-type: none;
}

.content {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

dt.project-name {
  font-weight: bold;
  margin-top: 10px;
}


dd.project-description {
  margin-left: 0px;
}
